export interface PositionerVariables {
  positioner: ViewStyle;
  modalContainer: ViewStyle;
}

export const getPositionerVariables = (theme: Theme): PositionerVariables => {
  return {
    modalContainer: {
      alignItems: 'center',
      display: 'flex',
      height: '100%',
      justifyContent: 'center',
      width: '100%',
    },
    positioner: {
      position: 'absolute',
      zIndex: 1,
    },
  };
};

